<template>
  <v-dialog v-model="dialog" persistent max-width="550">
    <template v-slot:activator="{ on, attrs }">
      <v-btn
        class="ma-2"
        outlined
        medium
        v-bind="attrs"
        v-on="on"
        color="primary"
      >
        <v-col cols="12"> <v-icon>mdi-plus</v-icon> add User </v-col>
      </v-btn>
    </template>
    <v-form ref="form" lazy-validation v-model="valid">
      <v-card>
        <v-card-title>
          <span class="text-h5">Add User</span>
        </v-card-title>
        <v-card-text>
          <v-container>
            <v-row>
              <v-col cols="12" sm="12" md="12">
                <v-text-field
                  prepend-inner-icon="mdi-account-outline"
                  :rules="nameRules"
                  label=" User name*"
                  required
                  v-model="user.name"
                  :counter="user.name"
                ></v-text-field>
              </v-col>

              <v-col cols="12">
                <v-text-field
                  prepend-inner-icon="mdi-email-outline"
                  :rules="emailRules"
                  label="Email*"
                  v-model="user.email"
                  required
                ></v-text-field>
              </v-col>
              <v-col cols="12">
                <v-text-field
                  v-model="user.password"
                  :rules="passwordRules"
                  :append-icon="value ? 'mdi-eye' : 'mdi-eye-off'"
                  @click:append="() => (value = !value)"
                  prepend-inner-icon="mdi-lock-outline"
                  label="Password *"
                  :type="value ? 'password' : 'text'"
                  :counter="user.password"
                  required
                ></v-text-field>
              </v-col>
              <!-- build role field  -->
              <v-col cols="12" sm="12">
                <v-autocomplete
                  v-model="user.role_id"
                  :rules="roleRules"
                  label="Implementation role"
                  :items="allRole"
                  item-text="name"
                  item-value="id"
                >
                  <template v-slot:selection="data">
                    <v-chip
                      v-bind="data.attrs"
                      :input-value="data.name"
                      close
                      :class="getColor(data.item.id)"
                      @click="data.select"
                      @click:close="remove(data.item)"
                    >
                      {{ data.item.name }}
                    </v-chip>
                  </template>
                </v-autocomplete>
              </v-col>
              <!--  -->
            </v-row>
          </v-container>
          <small>*indicates required field</small>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="blue darken-1" text @click="dialog = false">
            Close
          </v-btn>
          <v-btn color="secondary" text @click="addUser"
            ><span v-if="!progressAddUser">Add User</span>
            <v-progress-circular
              v-if="progressAddUser"
              color="primary"
              indeterminate
            ></v-progress-circular
          ></v-btn>
        </v-card-actions>
      </v-card>
    </v-form>
  </v-dialog>
</template>
<script>
import { userController } from "./user";
export default {
  methods: {
    remove() {
      this.user.role_id = null;
    },
    getRoles: async function () {
      var roles = await userController.getRoles();
      this.allRole = roles;
    },
    addUser: async function () {
      if (this.$refs.form.validate()) {
        this.progressAddUser = true;
        var state = await userController.addUser(this.user);
        this.progressAddUser = false;
        if (state) {
          this.dialog = false;
          this.$emit("getVal");
        }
      }
    },
    getColor(item) {
      // 1 is admin
      // 2 is donations_manager
      // 3 informative
      //  anther is user ...
      if (item == 1) {
        return "primary";
      } else if (item == 2) {
        return "secondary";
      } else if (item == 3) {
        return "red";
      } else {
        return "";
      }
    },
  },
  data() {
    return {
      valid: true,
      value: false,
      progressAddUser: false,
      allRole: [],
      roleRules: [(v) => !!v || "Choose at least role"],
      passwordRules: [
        (v) => !!v || "password is required",
        (v) => (v && v.length >= 8) || "the password is short  .... ",
        (v) => !/[ ]/.test(v) || "no spaces allowed",
      ],
      nameRules: [(v) => !!v || "the name field is required"],
      emailRules: [
        (v) => !!v || "E-mail is required",
        (v) => /.+@.+/.test(v) || "E-mail must be valid",
        (v) => !/[ ]/.test(v) || "no spaces allowed",
      ],
      user: {},
      dialog: false,
    };
  },
  watch: {
    dialog(val) {
      if (val) {
        this.getRoles();
        this.user.role_id = 4;
      }
    },
  },
};
</script>